$primary-color-dark: #00796B;
$primary-color: #009688;
$primary-color-light: #B2DFDB;
$primary-color-text: #FFFFFF;
$primary-text-color: #212121;
$secondary-text-color: #757575;
$divider-color: #BDBDBD;

$black: $primary-text-color;
$white: #fff;

$font-family-base: -apple-system,
BlinkMacSystemFont,
'Segoe UI',
Roboto,
Oxygen-Sans,
Ubuntu,
Cantarell,
'Helvetica Neue',
Helvetica,
Arial,
sans-serif;
$font-size-base: 1rem;
$font-weight-base: 400;
$line-height-base: 1.2;
$body-color: $black;
$body-bg: $white;

$transition: .4s ease;

$box-shadow-header: 0 4px 8px rgba(0, 0, 0, .15);
$box-shadow-article: 0 2px 10px rgba(20, 20, 20, .35);