.header {
    padding: 10px 0;
    background-color: $primary-color;
    color: $primary-color-text;
    box-shadow: $box-shadow-header;

    &__logo {
        flex-basis: 80px;

        img {
            max-width: 80px;
            max-height: 40px;
        }
    }

    &__row {
        align-items: center;
        flex-wrap: wrap;

        & > * {
            padding: 0 10px;
        }
    }

    @media (min-width: 768px) {
        &__row {
            flex-wrap: nowrap;
        }
    }
}

.nav {
    flex: 1 1 0;
    font-size: 1.4rem;

    &__list {
        display: flex;
        justify-content: flex-end;
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    &__item {
        margin: 0 .5rem;
    }

    &__link {
        color: inherit;

        &:hover,
        &:focus,
        &.is-active {
            color: $primary-color-light;
        }
    }
}