.comment-form {
    margin: .5rem 0;

    form {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }
}

.form-group {
    margin-bottom: 1rem;

    &__label {
        display: block;
        margin-bottom: .25rem;
        font-size: .85rem;
    }

    &--full {
        width: 100%;
    }

    &--half {
        width: 50%;
    }

    &--btn {
        margin-bottom: 0;
        text-align: right;
    }

    &.has-error {
        .input-field {
            border-color: #ff3c2d;
        }

        .form-group__label {
            color: #ff3c2d;
        }
    }
}

.input-field {
    padding: .4rem .8rem;
    border: 2px solid $divider-color;
    border-radius: 2px;

    &:focus {
        outline: none;
        border-color: $primary-color;
    }
}

.textarea-comment {
    width: 100%;
    height: 3.6em;
    resize: none;
}

.btn {
    display: inline-block;
    padding: .5rem 1rem;
    font-size: 1.2rem;
    cursor: pointer;
    text-align: center;
    text-transform: uppercase;
    border: 0;
    border-radius: 2px;
    background-color: $primary-color;
    color: $white;

    &:hover,
    &:focus {
        background-color: $primary-color-dark;
    }

    &:focus {
        outline: none;
    }

    &:active {
        background-color: $primary-color-dark;
        box-shadow: inset 0 2px 2px rgba(0, 0, 0, .25);
    }
}