@import 'local-variables';
@import 'reboot';
@import 'header';
@import 'footer';
@import 'form';

html {
    font-size: 12px;

    @media (min-width: 768px) {
        font-size: 13px;
    }

    @media (min-width: 992px) {
        font-size: 14px;
    }

    @media (min-width: 1220px) {
        font-size: 15px;
    }
}

h1 {
    font-size: 2.5rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.75rem;
}

h4 {
    font-size: 1.5rem;
}

h5 {
    font-size: 1.25rem;
}

h6 {
    font-size: 1rem;
}

.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 10px;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;
}

.col {
    position: relative;
    min-height: 1px;
    width: 100%;
    padding: 0 10px;

    &--side {
        order: -1;
    }

    @media (min-width: 992px) {

        &--center {
            flex: 0 0 75%;
            max-width: 75%;
        }

        &--side {
            order: 0;
            flex: 0 0 25%;
            max-width: 25%;
        }
    }
}


.main {
    padding: 3rem 0;

    @media (min-width: 768px) {
        min-height: calc(100vh - 115px);
    }
}

.article-label {
    display: inline-block;
    vertical-align: middle;
    padding: .1rem .4rem .2rem;
    font-size: .85rem;
    text-transform: uppercase;
    border: 1px solid $secondary-text-color;
    border-radius: 3px;
    color: $secondary-text-color;
}

.article-block {
    margin-bottom: 2rem;
    padding: 1rem;
    box-shadow: $box-shadow-article;
    border-radius: 5px;

    &__body {
        position: relative;
    }

    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 1rem;
        padding-top: .5rem;
        border-top: 1px solid $divider-color;
        color: $secondary-text-color;
    }

    &__title {
        margin-bottom: 1rem;
        font-size: 2rem;

        h2 {
            margin-bottom: 0;
        }

        a {
            color: $primary-color;

            &:hover,
            &:focus {
                color: $primary-color-dark;
            }
        }
    }

    &__category {
        position: absolute;
        top: 5px;
        right: 5px;
    }

    &__text {
        font-size: 1.2rem;
    }

    &__date {

    }

    &__comments {
        a {
            color: inherit;

            &:hover,
            &:focus {
                color: $primary-color;
            }
        }
    }
}

.title {
    overflow: hidden;

    &::after {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 100%;
        height: 2px;
        margin-right: -100%;
        right: -10px;
        content: '';
        background-color: $primary-color;
    }
}

.article-body {
    margin-bottom: 2rem;

    p {
        font-size: 1.3rem;
    }
}

.comment {
    margin-bottom: 1.2rem;
    padding-left: .5rem;
    border-left: 1px solid $primary-color;

    &__header {
        display: flex;
        align-items: center;
        color: $secondary-text-color;
    }

    &__name {
        margin-right: .5rem;
        font-size: 1.1rem;
    }

    &__date {
        font-style: italic;
    }

    &__text {
        font-size: 1.2rem;
    }
}

.category-block {
    margin-bottom: 2rem;
    font-size: 1.2rem;

    &__title {
        margin-bottom: .8rem;
        font-weight: 700;
    }

    &__item {
        margin-bottom: .5rem;

        a {

            &:hover,
            &:focus,
            &.is-active {
                color: $primary-color;
            }
        }
    }

    @media (min-width: 992px) {
        margin-bottom: 0;
    }
}